/** Table displaying classes **/
.d-table {
  display: table;
}

.d-inline-table {
  display: inline-table;
}

.d-table-cell {
  display: table-cell;
}

.vertical-align-middle {
  vertical-align: middle;
}

.vertical-align-baseline {
  vertical-align: baseline;
}

.vertical-align-bottom {
  vertical-align: bottom;
}

.vertical-align-sub {
  vertical-align: sub;
}

.vertical-align-super {
  vertical-align: super;
}

.vertical-text-top {
  vertical-align: text-top;
}

.vertical-text-bottom {
  vertical-align: text-bottom;
}
